










































































import { Component, Vue, Watch } from "vue-property-decorator";
import TodoItem from '@/interface/TodoItem'
import TodoStorage from '@/utils/TodoStorage'
import Filters from '@/utils/Filters'

enum ETab {
  all = 'all',
  active = 'active',
  completed = 'completed'
}

@Component({
  filters: {
    pluralize(n: number) {
      return n === 1 ? "item" : "items";
    }
  },
  directives: {
    'todo-focus'(el, binding) {
      if (binding.value) {
        el.focus();
      }
    }
  }
})
export default class App extends Vue {
  todos: TodoItem[] =  []
  newTodo: string = ''
  editedTodo: TodoItem | null = null
  visibility: ETab = ETab.all
  beforeEditCache: string = ''

  get filteredTodos() {
    return ['all', 'active', 'completed'].includes(this.visibility) ? Filters[this.visibility](this.todos) : [];
  }

  get remaining() {
    return Filters.active(this.todos).length;
  }

  get allDone() {
    return this.remaining === 0;
  }

  set allDone(value: boolean) {
    this.todos.forEach(function(todo) {
      todo.completed = value;
    })
  }

  @Watch('todos', { immediate: true, deep: true })
  onHandler(todos: TodoItem[]) {
    TodoStorage.save(todos);
  }

  updateVisbility(payload: ETab) {
    this.visibility = payload
  }

  addTodo(): void {
    var value = this.newTodo && this.newTodo.trim();
    if (!value) {
      return;
    }
    this.todos.push({
      id: TodoStorage.uid++,
      title: value,
      completed: false
    });
    this.newTodo = "";
  }

  removeTodo(todo: TodoItem) {
    this.todos.splice(this.todos.indexOf(todo), 1);
  }

  editTodo(todo: TodoItem) {
    this.beforeEditCache = todo.title;
    this.editedTodo = todo;
  }

  doneEdit(todo: TodoItem) {
    if (!this.editedTodo) {
      return;
    }
    this.editedTodo = null;
    todo.title = todo.title.trim();
    if (!todo.title) {
      this.removeTodo(todo);
    }
  }

  cancelEdit(todo: TodoItem) {
    this.editedTodo = null;
    todo.title = this.beforeEditCache;
  }

  removeCompleted() {
    this.todos = Filters.active(this.todos);
  }
}
